<template>
	<view>
		<!-- 地址 -->
		<view class="goods-section">
			<view class="g-header b-b">
				<image class="logo" src="http://duoduo.qibukj.cn/./Upload/Images/20190321/201903211727515.png"></image>
				<text class="name">每个人都可以过上想要的生活</text>
			</view>
			<!-- 商品列表 -->
			
			
			<view
				v-for="(item, index) in glist" :key="index"
				class="g-item"
				@click="navToDetailPage(item)"
			>
				<image :src="item.thumblist"></image>
				<view class="right">
					<text class="title clamp">{{item.attr}}</text>
					<text class="spec">{{item.attr}}</text>
					<view class="price-box">
						<text class="price">￥{{item.attrprice}}</text>
						<text class="number">x {{item.num}}</text>
					</view>
				</view>
			</view>
		</view>
		<navigator url="/pages/address/address?source=1" class="address-section">
			<view class="order-content" v-if="addressData.address">
				<text class="yticon icon-shouhuodizhi"></text>
				<view class="cen">
					<!-- <view class="top">
						<text class="name">{{addressData.name}}</text>
						<text class="mobile">{{addressData.mobile}}</text>
					</view> -->
					<text class="address">{{addressData.address}} {{addressData.area}}</text>
				</view>
				<text class="yticon icon-you"></text>
			</view>
			<view class="order-content" v-else>
				<text class="yticon" style="text-align:center;margin:auto;">选择地址</text>
			</view>
			

			<image class="a-bg" src="data:image/png;base64,"></image>
		</navigator>
		<!-- 优惠明细 -->
		<!-- <view class="yt-list">
			<view class="yt-list-cell b-b" @click="toggleMask('show')">
				<view class="cell-icon">
					券
				</view>
				<text class="cell-tit clamp">优惠券</text>
				<text class="cell-tip active">
					选择优惠券
				</text>
				<text class="cell-more wanjia wanjia-gengduo-d"></text>
			</view>
			<view class="yt-list-cell b-b">
				<view class="cell-icon hb">
					减
				</view>
				<text class="cell-tit clamp">商家促销</text>
				<text class="cell-tip disabled">暂无可用优惠</text>
			</view>
		</view> -->
		<!-- 金额明细 -->
		<view class="yt-list">
			<view class="yt-list-cell b-b">
				<text class="cell-tit clamp">每小时价格:</text>
				<text class="cell-tip">¥{{total}}</text>
			</view>
			<view class="yt-list-cell b-b">
				<text class="cell-tit clamp">预定时间:</text>
				<text class="cell-tip">{{startTime}}</text>
			</view>
			<!-- <view class="yt-list-cell b-b">
				<text class="cell-tit clamp">优惠金额</text>
				<text class="cell-tip red">-￥35</text>
			</view>
			<view class="yt-list-cell b-b">
				<text class="cell-tit clamp">运费</text>
				<text class="cell-tip">免运费</text>
			</view> -->
			<view class="yt-list-cell desc-cell">
				<text class="cell-tit clamp">电话</text>
				<input class="desc" type="text" v-model="mobile"  placeholder="请填写手机号码" placeholder-class="placeholder" />
				<text class="cell-tip">配送电话联系</text>
				<!-- <input class="desc" type="text" v-model="desc" placeholder="请填写手机号码" placeholder-class="placeholder" /> -->
			</view>
			<view class="yt-list-cell desc-cell">
				<text class="cell-tit clamp">备注</text>
				<input class="desc" type="text" v-model="desc" placeholder="请填写备注信息" placeholder-class="placeholder" />
			</view>
			<view class="yt-list-cell desc-cell">
				<text>{{orderlist.descript}}</text>
			</view>
		</view>
		
		<!-- 底部 -->
		<view class="footer">
			<view class="price-content">
				<text>时价</text>
				<text class="price-tip">￥</text>
				<text class="price">{{orderlist.price}}</text>
			</view>
			<text class="submit" @click="submit">确认订单</text>
		</view>
		
		<!-- 优惠券面板 -->
		<view class="mask" :class="maskState===0 ? 'none' : maskState===1 ? 'show' : ''" @click="toggleMask">
			<view class="mask-content" @click.stop.prevent="stopPrevent">
				<!-- 优惠券页面，仿mt -->
				<view class="coupon-item" v-for="(item,index) in couponList" :key="index">
					<view class="con">
						<view class="left">
							<text class="title">{{item.title}}</text>
							<text class="time">有效期至2019-06-30</text>
						</view>
						<view class="right">
							<text class="price">{{item.price}}</text>
							<text>满30可用</text>
						</view>
						
						<view class="circle l"></view>
						<view class="circle r"></view>
					</view>
					<text class="tips">限新用户使用</text>
				</view>
			</view>
		</view>

	</view>
</template>

<script>
	import store from '@/store/index.js';
	import share from '@/components/share';
	import {
	    mapState,mapMutations
	} from 'vuex';
	export default {
		data() {
			return {
				maskState: 0, //优惠券面板显示状态
				desc: '', //备注
				payType: 1, //1微信 2支付宝
				from: 1,//1从购物车 2直接
				cartindex: [],//如果from为 1  则 清理这里购物车内容
				cartList: [],//购物车内容
				glist: [],
				mobile: '',
				startTime: '',
				couponList: [
					{
						title: '新用户专享优惠券',
						price: 5,
					},
					{
						title: '庆五一发一波优惠券',
						price: 10,
					},
					{
						title: '优惠券优惠券优惠券优惠券',
						price: 15,
					}
				],
				orderlist:[],
				addressData:[],
				total:''
			}
		},
		onLoad(option){
			// 商品数据
			let data = JSON.parse(option.data);
			console.log('onLoad');
			 console.log(data.goodsData);
			this.loadOrder(data);
			
		},
		computed: {
			...mapState(['hasLogin','userInfo','hasCart','cartInfo','hasCart'])
		},
		
		methods: {
			//显示优惠券面板
			...mapMutations(['login','clearCartAll','editCart','setOrder']),
			toggleMask(type){
				let timer = type === 'show' ? 10 : 300;
				let	state = type === 'show' ? 1 : 0;
				this.maskState = 2;
				setTimeout(()=>{
					this.maskState = state;
				}, timer)
			},
			numberChange(data) {
				this.number = data.number;
			},
			changePayType(type){
				this.payType = type;
			},
			async submit(){
				// 这里下订单 后台交互 。 生成订单  扣掉库存 。 生成支付单号（默认是等待支付 。 如果一定时间不支付则取消 并恢复库存)。跳转到支付页面。 等待反馈。 
				let address = this.addressData;
				let order   = this.orderlist;
				let total = this.total;
				let mobile = this.mobile;
				let desc   = this.desc;
				
				console.log('address');
				console.log(this.addressData);
				
				if(!this.addressData.address){
					this.$alert('别忘了选地址哦'); 
					return;
				}
				
				if(!this.mobile){
					this.$alert('电话号码留着好联系哦,我们将提前与您联系'); 
					return;
				}
				// console.log('this.addressData:'+JSON.stringify(address));
				// console.log('this.orderlist:'+JSON.stringify(order));
				// console.log('this.total:'+JSON.stringify(total))
				//订单信息 价格  地址      还缺一个优惠券信息。
				//提交给api 处理 生成订单 + 支付单 + 锁定库存 
				/**
				 *  buildOrder 需要再一次确认  
				 *  1.商品库存是否足够。
				 *  2.足够则扣库存 （stock_locked) 锁定 。 支付后再扣。 
				 *  3. 生成订单信息。 等待支付。
				 *  4. 支付数据 
				
				 */
				let userInfo = this.userInfo;
				let startTime = this.startTime;
				const sendData = {
					order,
					address,
					total,
					userInfo,
					mobile,
					desc,
					startTime,
				};
				console.log('sendData');
				console.log(sendData);
				const res = await this.$myRequest({url: '/api/order/buildGroupOrder',method:'post',data: sendData});
				console.log('buildOrder');
				if(res.data.error == 1){
					this.$alert(res.data.msg); 
					return;
				}
				if(res.data.error == 0){//这里没有错的话。 就记录。
				
					this.setOrder(res.data.res);
					this.$alert(res.data.msg); 
					uni.redirectTo({
						url: '/pages/order/orderDesc?id='+res.data.url
					})
					return;
				}
				console.log(res.data.res);
//
				return;
				uni.redirectTo({
					url: '/pages/money/pay'
				})
			},
			async loadOrder(option){
				console.log('createOrder:');
				console.log(JSON.stringify(option));
				const res = await this.$myRequest({url: '/api/order/createGroupOrder',method:'post',data:JSON.stringify(option)});	
				console.log('createOrder');
				console.log(res.data);//这里得到数据并赋值给数据
				
				//创建后需要删除对应的key（如果有的话？）还需要区分是从购物车来的。 还是直接下单。 如果直接下单则无视。
				console.log('ok');
				console.log(res.data.startTime);
				this.startTime = res.data.startTime;
				this.from = res.data.from;
				this.cartindex = res.data.cartindex;
				console.log('cartindex:'+JSON.stringify(res.data.cartindex));
				//赋值
				this.orderlist = res.data.res;
				
				let tempgoodslist = JSON.parse(res.data.res.goodslist);
					
					
				this.glist = [];
				tempgoodslist.forEach(item=>{ 
						//if(item.selected === true){ 
							this.glist.push(item); 			
						//} 
				})
				this.total = res.data.res.price;
				 if(res.data.address){
					 this.addressData = res.data.address;
				 };
				
			},
			stopPrevent(){}
			
		}
	}
</script>

<style lang="scss">
